import "../styles/App.css";
import { useEffect } from "react";

function Contact() {
  // window.scrollTo(0, 0);

  // Form Data Submit Funtion
  function logURL() {
    console.log(window.location.hash);
  }
  //

  const FormSubmit = (e) => {
    e.preventDefault();

    let Email = document.querySelector("#Form input").value,
      Message = document.querySelector("#Form textarea").value;

    let dataEntry = {
      "entry.118775800": Email,
      "entry.1799604411": Message,
    };

    let dataEntryParams = new URLSearchParams(dataEntry),
      DataEntryString = dataEntryParams.toString();

    //
    // Push Data Values
    var dataPush = new XMLHttpRequest();
    dataPush.open(
      "POST",
      "https://docs.google.com/forms/u/3/d/e/1FAIpQLSf748oXmA0Icy-h6gYuM2hWl2TMhepdVqIgAUqFDJClZNOh1w/formResponse",
      true
    );

    dataPush.setRequestHeader(
      "Content-type",
      "application/x-www-form-urlencoded"
    );

    console.log(DataEntryString);
    dataPush.send(DataEntryString);

    let Loadingcircle = document.querySelector("#loadingcirle"),
      loadingText = document.querySelector("#Form button");

    Loadingcircle.style.display = `block`;
    Loadingcircle.style.opacity = `1`;
    loadingText.style.color = `transparent`;

    let SubmitAwait = setTimeout(() => {
      window.location.href = "./#/ThankYou";
      console.log("Thank You!!!");

      clearTimeout(SubmitAwait);
    }, 3333);
  };

  // Use Effect Hooks
  const ContactInit = () => {
    //
    function ContactLink() {
      let NavEls = document.querySelectorAll(".links li span"),
        NavElsText = document.querySelectorAll(".links li"),
        ContactLinkSpan = document.querySelector("#ContactLink span"),
        ContactLinkText = document.querySelector("#ContactLink");

      NavEls.forEach((Element) => {
        Element.style = "";
      });
      NavElsText.forEach((Element) => {
        Element.style = "";
      });

      ContactLinkSpan.style.height = `100%`;
      ContactLinkText.style.color = `#151515`;
    }
    ContactLink();
    //
    //
    // const Mapscripts = document.createElement("script");

    // Mapscripts.src =
    //   "https://maps.googleapis.com/maps/api/js?key=AIzaSyDI1Bt5fcHnAeU5hc1Q6yswi6qwe3N0P3w&callback=initMap&v=weekly";
    // Mapscripts.async = true;

    //
    //
    const VDFM_map = document.querySelector("#VDFM_map");
    const MapsInit = document.createElement("script");

    MapsInit.src = "https://alpha.vdfm.ga/map.js";
    MapsInit.async = true;

    // document.body.appendChild(Mapscripts);
    VDFM_map.appendChild(MapsInit);
    //
    //
  };

  useEffect(ContactInit, []);

  return (
    <main className="Contact">
      <section className="Section" id="Map_Header">
        <div id="VDFM_map"></div>
        <div id="info_windows" onClick={logURL}>
          <h1>VDFM HÀ NỘI</h1>
          <h3>
            TẦNG 06, TÒA NHÀ SÔNG ĐÀ 11 131 - Km10, Đường Nguyễn Trãi, Q.Hà
            Đông, Hà Nội
          </h3>
          <a href="tel:+84365617776" className="marked">
            Hotline:&nbsp;+84365617776
          </a>
          <a href="tel:+84365617776" className="marked">
            Máy lẻ:&nbsp;62.322
          </a>
        </div>
        {/* <script
          src="https://maps.googleapis.com/maps/api/js?key=AIzaSyDI1Bt5fcHnAeU5hc1Q6yswi6qwe3N0P3w&callback=initMap&v=weekly"
          async
        ></script>
        <script src="https://alpha.vdfm.ga/map.js" async></script> */}
      </section>
      <section className="Section" id="Contact_title">
        <h1>Liên hệ</h1>
        <div className="mailIcon2"></div>
      </section>
      <section className="Section" id="Contact_Info">
        <div className="Contact_col" id="Coop">
          <h2>Hợp tác truyền thông</h2>
          <p>
            Địa chỉ: TẦNG 06, TÒA NHÀ SÔNG ĐÀ 11 131 - Km10, Đường Nguyễn Trãi,
            Trần Phú, Q.Hà Đông, Hà Nội
          </p>
          <a
            href="mailto:anhnm.b18tt006@stu.ptit.edu.vn"
            id="Email"
            className="marked"
          >
            Email:&nbsp;letter@vdfmag.com
          </a>
          <a href="tel:+84365617776" className="marked">
            Hotline:&nbsp;+84365617776
          </a>
          <a href="tel:+84365617776" className="marked">
            Máy lẻ:&nbsp;62.322
          </a>
        </div>
        <div className="Ver_line"></div>
        <div className="Contact_col" id="Form">
          <h2>Góp ý, câu hỏi, báo lỗi</h2>
          <form action="" method="POST" onSubmit={FormSubmit}>
            <div className="name-input" id="mail">
              <input
                type="text"
                name="email"
                autoComplete="off"
                spellCheck="false"
                placeholder=""
                required
              />
              <label htmlFor="email" className="name-label">
                <span className="name-content">Email</span>
              </label>
            </div>

            <div className="name-input" id="message">
              <textarea
                name="message"
                id="message-inp"
                autoComplete="off"
                spellCheck="false"
                required
              ></textarea>
              <label htmlFor="message" className="name-label">
                <span className="name-content">Nội dung</span>
              </label>
            </div>
            <button type="submit">
              Gửi
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 239.62 239.61"
                id="loadingcirle"
              >
                <circle
                  className="loadingcirle-1"
                  cx="119.81"
                  cy="119.81"
                  r="109.81"
                />
              </svg>
            </button>
          </form>
        </div>
      </section>
    </main>
  );
}
export default Contact;
