import "./styles/App.css";
import { useEffect } from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";

// Elements
import Nav from "./Elements/nav";
import HomePage from "./Elements/HomePage";
import InVietNam from "./Elements/InVietNam";
import DigitalFashion from "./Elements/DigitalFashion";
import Contact from "./Elements/Contact";
import About from "./Elements/About";
import WhatNew from "./Elements/WhatNew";
import VirtualInfluencer from "./Elements/VirtualInfluencer";
import Footer from "./Elements/Footer";
import FourOhFour from "./Elements/404er";
import ThankYou from "./Elements/ThankYou";
import NewsTemp from "./Elements/NewsTemp";

//
// Main Function
//
function App() {
  //
  //Preload
  useEffect(function Preload() {
    scrollLoad();
  }, []);

  //Scroll Reset
  const scrollLoad = () => {
    // Grab The Elements
    const body = document.body;
    const main = document.getElementById("main_scroll");
    // const center_nav = document.querySelector(".nav_container .center_nav");

    // Constructing Values
    let // sx = 0, // For scroll positions
      sy = 0;
    let // dx = sx, // For container positions And Force (Percentage 70% Recommended)
      dy = sy,
      Force = 70;

    // Onpage Load And Refresh Events

    body.style.height = main.clientHeight + "px";

    // ResetPage();

    // window.addEventListener("beforeunload", (e) => {
    //   ResetPage();
    // });

    // function ResetPage() {
    //   window.scrollTo(0, 0);

    //   body.scrollTo(0, 0);
    //   main.style = ``;
    // }

    function ScrollResize() {
      let bodyHeight = main.getBoundingClientRect().height;
      body.style.height = `${bodyHeight}px`;
      easeScroll();
      //
      var resizetime = setTimeout(() => {
        bodyHeight = main.getBoundingClientRect().height;
        body.style.height = `${bodyHeight}px`;
        easeScroll();

        clearTimeout(resizetime);
      }, 1500);
    }
    window.addEventListener("resize", ScrollResize);
    window.addEventListener("mousedown", ScrollResize);

    //
    // Momentum Scrolling

    // Scroll Functions
    window.addEventListener("scroll", (e) => {
      easeScroll();

      // HeadersResponse();
    });

    function easeScroll() {
      // sx = window.pageXOffset;
      sy = window.pageYOffset;
    }

    window.requestAnimationFrame(render);

    function render() {
      //We calculate our container position by linear interpolation method
      // dx = li(dx, sx, Force / 1000);
      dy = li(dy, sy, Force / 1000);

      // dx = Math.floor(dx * 100) / 100;
      dy = Math.floor(dy * 100) / 100;

      main.style.transform = `translateY(-${dy}px)`;

      window.requestAnimationFrame(render);
    }

    function li(a, b, n) {
      return (1 - n) * a + n * b;
    }
  };

  return (
    <Router basename="" hashType="slash">
      <div className="App">
        <Nav /> {/* Navigation Bar */}
        <div className="Stage" id="main_scroll">
          <Switch>
            <Route path="/" exact component={HomePage} />
            <Route path="/Tai-Viet-Nam" exact component={InVietNam} />
            <Route path="/Thoi-trang-ao" exact component={DigitalFashion} />
            <Route path="/Lien-he" exact component={Contact} />
            <Route path="/Ve-chung-toi" exact component={About} />
            <Route
              path="/Virtual-influencer"
              exact
              component={VirtualInfluencer}
            />
            {/*  */}
            <Route path="/Tin-moi" exact component={WhatNew} />
            <Route path="/Tin-moi/:id" component={NewsTemp} />
            {/* Thank You Page */}
            <Route path="/ThankYou" exact component={ThankYou} />
            {/*  */}
            {/*  */}
            {/* 404 Page Not Found */}
            <Route path="*" component={FourOhFour} />
            <Route path="/404" component={FourOhFour} />
            {/* 404 Page Not Found */}
            {/*  */}
          </Switch>
          {/* Footer Zone */}
          <Footer />
          {/* Footer Zone */}
        </div>
      </div>
    </Router>
  );
}

export default App;
