import "../styles/App.css";
import { Link } from "react-router-dom";
import { useEffect } from "react";
//

function FourOhFour() {
  //
  window.scrollTo(0, 0);
  //
  //

  function FourOhFourink() {
    let NavEls = document.querySelectorAll(".links li span"),
      NavElsText = document.querySelectorAll(".links li");

    NavEls.forEach((Element) => {
      Element.style = "";
    });
    NavElsText.forEach((Element) => {
      Element.style = "";
    });
  }
  FourOhFourink();
  //
  useEffect(() => {
    let body2 = document.body,
      main2 = document.getElementById("main_scroll"),
      timeOut = setTimeout(() => {
        body2.style.height = main2.clientHeight + "px";
        clearTimeout(timeOut);
      }, 1111);
    body2.style.height = main2.clientHeight + "px";
  }, []);

  return (
    <main className="FourOhFour">
      <h1>404</h1>
      <h3>Thôi toang rồi!</h3>
      <div className="Hor_line"></div>
      <p>
        Liên kết này không chính xác hoặc có thể đã xóa.
        <br />
        Nếu bạn nghĩ có thứ gì đó sai sai, hãy báo lỗi tới chúng tôi.
      </p>
      <div className="row">
        <Link to="/">
          <div className="Return_Home_Button">Trang Chủ</div>
        </Link>
        <Link to="/Lien-he">
          <div className="Report_Button">Báo lỗi</div>
        </Link>
      </div>
    </main>
  );
}

export default FourOhFour;
