import "../styles/App.css";
// import { Link } from "react-router-dom";
import { useEffect } from "react";
import IframeResizer from "iframe-resizer-react";

//

function NewsTemp() {
  //

  var News = {
    //
    SourceURL:
      "https://alpha.vdfm.ga/Public/Chao-mung-den-voi-thoi-dai-cua-Metaverse/",
    URLSubfix: "Chao-mung-den-voi-thoi-dai-cua-Metaverse",

    Date: "27/10/2021",
    Auth: "Mai Anh",
    AuthSubfix: " , theo Style-Republik",
    SubfixURL:
      "https://style-republik.com/metaverse-va-thoi-trang-ky-thuat-so/",

    ViewCount: "12k",

    Thumbnail:
      "https://teknoloji.news/wp-content/uploads/2021/10/CROPPED-scaled-1.jpeg",

    Title: "Chào mừng đến với thời đại của Metaverse",
    Chapeau:
      "Metaverse là một phiên bản cấp tiến của Internet, một vũ trụ ảo nơi con người thật sự “tồn tại” trong đó. Đây chính là một ý tưởng đột phá, một môi trường ảo có rất nhiều tiềm năng phát triển",
  };
  //
  var MailToURL =
    "mailto:?&" +
    "subject=[VDFM.ga] " +
    News.Title +
    "&body=" +
    News.SourceURL +
    News.Chapeau;
  //
  // href="/sharer/sharer.php?kid_directed_site=0&sdk=joey&u=https%3A%2F%2Falpha.vdfm.ga%2F%23%2FTin-moi%2FChao-mung-den-voi-thoi-dai-cua-Metaverse&display=popup&ref=plugin&src=share_button"
  //
  var ShareURL =
    "https://www.facebook.com/" +
    "/sharer/sharer.php?kid_directed_site=0&sdk=joey&u=https%3A%2F%2Falpha.vdfm.ga%2F%23%2FTin-moi%2F" +
    News.URLSubfix +
    "&display=popup&ref=plugin&src=share_button";
  // //

  function PostMessage(e) {
    e.preventDefault();
    // let iframeel = document.querySelector(".MainNews iframe").contentWindow;
    // iframeel.PostMessage("hello News!", "*");

    //
    if (window && window.parent) {
      window.parent.postMessage(
        {
          message: "The message is being set up here",
          hide: "dbhchat",
          show: "dbhchat",
        },
        "*"
      );
    }
  }

  //
  function NewsTempLink() {
    let NavEls = document.querySelectorAll(".links li span"),
      NavElsText = document.querySelectorAll(".links li");

    NavEls.forEach((Element) => {
      Element.style = "";
    });
    NavElsText.forEach((Element) => {
      Element.style = "";
    });
  }
  NewsTempLink();
  //
  window.scrollTo(0, 0);
  //
  useEffect(() => {
    let body2 = document.body,
      main2 = document.getElementById("main_scroll");

    body2.style.height = main2.clientHeight + "px";

    var timeOut = setTimeout(() => {
      body2.style.height = main2.clientHeight + "px";
      clearTimeout(timeOut);
    }, 1111);
  }, []);

  return (
    <main className="NewsTemp">
      <section className="Section" id="NewsHeaderTemp">
        <div
          className="NewsThumb"
          style={{ backgroundImage: `url("${News.Thumbnail}")` }}
        ></div>
        <div className="NewsContents">
          <h2 id="NewsTitle">{News.Title}</h2>
          <div className="Hor_line"></div>
          <div className="NewsMetaGroup">
            <h3>
              <span></span>
              {News.Date}&nbsp;-&nbsp;từ&nbsp;<strong>{News.Auth}</strong>
              <a href={News.SubfixURL} target="_blank" rel="noreferrer">
                {News.AuthSubfix}
              </a>
            </h3>
            <div className="MetaAndShare">
              <div id="ViewCount">{News.ViewCount}</div>
              <a
                title="fb_share"
                href={ShareURL}
                target="_blank"
                rel="noreferrer"
              >
                <div id="FacebookShare"></div>
              </a>
              <a title="SendMail" href={MailToURL} rel="nofollow">
                <div id="Email"></div>
              </a>
            </div>
          </div>
        </div>
      </section>
      <section className="Section" id="NewsLayout">
        <div className="row">
          <div className="MainNews">
            {/* <iframe
              src={News.SourceURL}
              frameBorder="0"
              // scrolling="no"
              onLoad={SetIframeHeight}
              id="mainframe"
              ref={IframeElement}
            ></iframe> */}
            <IframeResizer
              heightCalculationMethod="bodyScroll"
              frameBorder="0"
              name="News"
              src={News.SourceURL}
              loading="lazy"
              onLoad={PostMessage}
            />
          </div>
          <div className="DockedLayout">
            <div className="DockItem" id="item1"></div>
          </div>
        </div>
      </section>
    </main>
  );
}

export default NewsTemp;
