import "../styles/App.css";
import { useEffect, useState } from "react";

function InVietNam() {
  //    window.scrollTo(0, 0);

  //
  const [IsListLayout, setLayout] = useState(true);
  //
  const SwapLayout = () => {
    setLayout(!IsListLayout);
  };
  //

  const InVNEff = () => {
    //
    //
    function NavInVNLink() {
      let NavEls = document.querySelectorAll(".links li span"),
        NavElsText = document.querySelectorAll(".links li"),
        InVNLinkSpan = document.querySelector("#InVNLink span"),
        InVNLinkText = document.querySelector("#InVNLink");

      NavEls.forEach((Element) => {
        Element.style = "";
      });
      NavElsText.forEach((Element) => {
        Element.style = "";
      });

      InVNLinkSpan.style.height = `100%`;
      InVNLinkText.style.color = `#151515`;
    }
    NavInVNLink();
    //
    const C2_Image = document.querySelectorAll(".C2_Image");
    var FadeCount = 0;
    const CarrouselFade = () => {
      //
      C2_Image[FadeCount].style = "";
      //
      FadeCount++;
      //
      if (FadeCount === C2_Image.length) {
        FadeCount = 0;
      }
      //
      C2_Image[FadeCount].style.transform = `scale(1.18)`;
      C2_Image[FadeCount].style.opacity = `1`;
    };

    CarrouselFade();
    var Carrousel_Fade_Timer = setInterval(CarrouselFade, 8000);
    //
    window.addEventListener("blur", () => {
      clearInterval(Carrousel_Fade_Timer);
    });
    window.addEventListener("focus", () => {
      clearInterval(Carrousel_Fade_Timer);
      CarrouselFade();
      Carrousel_Fade_Timer = setInterval(CarrouselFade, 8000);
    });

    //
    //
  };

  useEffect(InVNEff, []);

  return (
    <main className="InVietNam">
      <section className="Section" id="Carrousel_Fade">
        <div className="C2_Image_Container">
          <div className="C2_Image" id="IMG1"></div>
          <div className="C2_Image" id="IMG2"></div>
          <div className="C2_Image" id="IMG3"></div>
          <div className="C2_Image" id="IMG4"></div>
          <div className="C2_Image" id="IMG5"></div>
        </div>

        <h2>Thời trang ảo</h2>
        <h1>Tại Việt Nam</h1>
      </section>
      <section className="Section" id="sec2">
        <div
          className={
            IsListLayout ? "LayOut_Swap" : "LayOut_Swap LayOut_Swap_Grid"
          }
          onClick={SwapLayout}
        >
          <div className="Overlay_Swap"></div>
          <div className="Swapp_button" id="OutBound"></div>
          <div className="Swapp_button" id="InBound"></div>
        </div>
        <div className={IsListLayout ? "OutBoundLayout" : "InBoundLayout"}>
          <div className="Item" id="sticky1">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">Thách thức của thời trang Việt Nam</h2>
              <h6 className="meta">- Hôm nay</h6>
              <h4 className="descriptions">
                Nền kinh tế, ngân sách hạn hẹp; việc áp dụng công nghệ kỹ thuật
                còn chưa cao; người tiêu dùng chưa có đủ nhận thức và định kiến
                “khuôn mẫu” về thời trang - thực tế; và cả vấn đề bản quyền ...
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div>
          <div className="Item" id="sticky2">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">Virtual Influencer bắt nguồn từ đâu?</h2>
              <h6 className="meta">- 3 ngày trước</h6>
              <h4 className="descriptions">
                Influencer là người có tầm ảnh hưởng, có khả năng ảnh hưởng đến
                quyết định, hành vi mua hàng của nhóm khách hàng mục tiêu. Những
                người này thường có một mức độ nổi tiếng nhất định, hoặc có kiến
                thức và địa vị đối với nhóm đối tượng khán giả của họ...
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div>{" "}
          <div className="Item" id="sticky3">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">
                Gặp gỡ Virtual Influencers bậc nhất thế giới hiện nay
              </h2>
              <h6 className="meta">- 1 Tuần trước</h6>
              <h4 className="descriptions">
                Shudu Gram - Siêu mẫu kỹ thuật số đầu tiên
                <br /> Lil Miquela - đứa con tinh thần của Brud <br /> Bermuda{" "}
                <br />
                Blawko Imma
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div>{" "}
          <div className="Item" id="sticky4">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">
                Tính bền vững của Thời trang kỹ thuật số
              </h2>
              <h6 className="meta">- 1 tháng trước</h6>
              <h4 className="descriptions">
                Dịch bệnh khiến cho khái niệm về “tính bền vững” đến gần với các
                nhà thiết kế nói riêng và công chúng nói chung. Tính bền vững -
                dừng lãng phí và tiết kiệm hơn - “thắt chặt trong khâu lựa chọn
                nguyên liệu” ...
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div>
          <div className="Item even" id="sticky5">
            <div className="thumbnail_square">
              <div className="thumbnail"></div>
            </div>
            <div className="contents">
              <h2 className="title">
                Xây dựng ngôi nhà thời trang kỹ thuật số
              </h2>
              <h6 className="meta">- 1 tháng trước</h6>
              <h4 className="descriptions">
                Những yếu tố cần thiết để thương hiệu Việt bắt kịp xu hướng thời
                trang thế giới Từ những thiếu sót của mình chỉ ra hướng đi cho
                các thương hiệu Việt Nam ...
              </h4>
            </div>
            <div className="view_count">500k</div>
          </div>
          <div className="show_more_but">
            <h3>Hiện thêm</h3>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 780 350">
              <polyline className="arr-1" points="5 345 390 5 775 345" />
            </svg>
          </div>
        </div>
      </section>
    </main>
  );
}
export default InVietNam;
