import "../styles/App.css";
import { Link } from "react-router-dom";
import { useEffect } from "react";
//

function ThankYou() {
  //

  function ThankYouLink() {
    let NavEls = document.querySelectorAll(".links li span"),
      NavElsText = document.querySelectorAll(".links li");

    NavEls.forEach((Element) => {
      Element.style = "";
    });
    NavElsText.forEach((Element) => {
      Element.style = "";
    });
  }
  ThankYouLink();
  //
  window.scrollTo(0, 0);
  //
  useEffect(() => {
    let body2 = document.body,
      main2 = document.getElementById("main_scroll");

    body2.style.height = main2.clientHeight + "px";

    var timeOut = setTimeout(() => {
      body2.style.height = main2.clientHeight + "px";
      clearTimeout(timeOut);
    }, 1111);
  }, []);

  return (
    <main className="ThankYou">
      <div id="overlay_matte"></div>
      <h1>Lời cám ơn</h1>
      <h3>Từ VDFM</h3>
      <div className="Hor_line"></div>
      <p>
        Hãy nhớ rằng: "Đóng góp từ độc giả <br /> Chính là nguồn cảm hứng của
        chúng tôi"
      </p>
      <div className="row">
        <Link to="/">
          <div className="Return_Home_Button">Trang Chủ</div>
        </Link>
      </div>
    </main>
  );
}

export default ThankYou;
